.wrapper {
  position: fixed;
  z-index: 97;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  display: flex;
}

.sideBarAd__list {
}

.sideBarAd__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: var(--text-color-white);
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size12);
  text-transform: uppercase;
  width: 70px;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  margin-bottom: 5px;

  &:hover {
    background-color: #084f75;
    cursor: pointer;
  }
}
.sideBarAd__itemHighLight {
  font-size: var(--font-size20);
}

.sideBarAd__demos {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: red;
}

.sideBarAd__demosCloseBtn {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 10px;
  margin-top: 10px;
  font-size: var(--font-size24);
  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }
}

/* Mobile */
@media (max-width: 739px) {
  .wrapper {
    display: none;
  }
}
